
"use client";

import { useState, useEffect } from "react";
import { LabVisitsChart } from "@/components/charts/lab-visits-chart";
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from "@/components/ui/card";
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table";
import { Badge } from "@/components/ui/badge";
import { labTestManager, LabTest } from "@/lib/constants";

export default function LabTechDashboard() {
  const [pendingTests, setPendingTests] = useState<LabTest[]>([]);

  useEffect(() => {
      const handleUpdate = (updatedTests: LabTest[]) => {
          setPendingTests(updatedTests.filter(t => t.status !== 'Completed'));
      };
      handleUpdate(labTestManager.getLabTests());
      const unsubscribe = labTestManager.subscribe(handleUpdate);
      return () => unsubscribe();
  }, []);

  return (
    <div className="grid gap-6 md:grid-cols-1 lg:grid-cols-2">
      <LabVisitsChart />
      <Card>
        <CardHeader>
          <CardTitle>Pending Lab Tests</CardTitle>
          <CardDescription>Tests awaiting processing or results upload.</CardDescription>
        </CardHeader>
        <CardContent>
           <Table>
            <TableHeader>
              <TableRow>
                <TableHead>Patient</TableHead>
                <TableHead>Test</TableHead>
                <TableHead>Status</TableHead>
              </TableRow>
            </TableHeader>
            <TableBody>
              {pendingTests.map((t) => (
                <TableRow key={t.id}>
                  <TableCell className="font-medium">{t.patient}</TableCell>
                  <TableCell>{t.test}</TableCell>
                   <TableCell>
                    <Badge variant={
                        t.status === 'Pending' ? 'destructive' : t.status === 'Processing' ? 'secondary' : 'default'
                    } className={t.status === 'Completed' ? 'bg-green-500' : ''}>{t.status}</Badge>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </CardContent>
      </Card>
    </div>
  );
}
